html { box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; -webkit-clip-path: inset(100%); clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

html {font-size: calc(5px + (10 - 5) * (100vw - 320px) / (1600 - 320))}
body {font-size: 1.6rem;margin: 0}
h1, h2, h3, h4, h5, h6, p, ul {margin: 0}
ul {padding: 0; list-style-type: none}
a {text-decoration: none;color: #000;display: inline-block}

body {
  background-color: #000;
  font-family: 'Fira Code', monospace;
  font-weight: 300;
  position: relative;
  min-height: 100vh;
}

*:focus {
  outline: none;
}

a {
  color: inherit;
}

section {
  width: 85%;
  height: 100vh;
  position: fixed;
  left: -3000px;
  background-color: #000;
  transition: all 1s;
  z-index: 0;
  overflow: hidden;
}

section.show {
  left: 15%;
}

.app {
  display: flex;
}

ul.nav {
  width: 15%;
  min-height: 100vh;
  background-color: rgb(66,66,66);
  position: absolute;
  z-index: 10;
  border-right: 2px #000 solid;
}

ul.nav li {
  padding: 4rem 3rem 8rem 3rem;
  font-size: 3rem;
  height: 5rem;
  border-bottom: solid 2px #000;
  transition: all 0.3s;
}

ul.nav li:hover,
ul.nav li:focus,
ul.nav li.selected {
  background-color: #000;
  cursor: pointer;
  color: silver;
}

.home h1 {
  color: silver;
  font-weight: 700;
  font-size: 5rem;
  text-align: center;
  padding-top: 35vh;
  margin: 0rem 3rem 0rem 3rem;
}

.home h2{
  color: silver;
  text-align: center;
  margin: 5rem;
}

.home footer {
  color: silver;
  position: absolute;
  bottom: 15px;
  right: 20px;
}

section.messenger {
  padding: 5rem;
}

.messenger h2 {
  text-align: right;
  color: silver;
  margin-bottom: 2rem;
}

.messenger .scrollDummy {
  height: 1px;
}

.messenger .showMsg {
  padding: 3rem;
  width: 100%;
  height: 58vh;
  overflow: scroll;
  background-color: rgb(66,66,66);
  border-radius: 10px;
  overflow-x: hidden;
}

.messenger ul.oldMsg {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.messenger ul.oldMsg li {
  padding: 1.5rem;
  margin: 0.8rem auto;
  border-radius: 10px;
  background-color: rgb(33,33,33);
  color: silver;
  font-size: 1.5rem;
  display: inline;
}

.messenger ul.oldMsg .userName {
  opacity: 0.4;
  margin-bottom: 1rem;
  text-align: center;
}

.messenger form.send .msgText {
  display: flex;
  align-items: center;
}

.messenger .sendMsg input,
.messenger .sendMsg textarea {
  font-family: inherit;
  margin-top: 20px;
  background-color: rgb(66,66,66);
  border: none;
  transition: all 0.3s;
  color: silver;
  padding: 2rem;
}

.messenger .sendMsg input {
  display: block;
  border-radius: 10px;
  height: 2rem;
}

.messenger .sendMsg textarea,
.messenger .sendMsg button {
  height: 18vh;
}

.messenger .sendMsg textarea {
  width: 90%;
  resize: none;
  border-radius: 10px 0px 0px 10px;
}

.messenger .sendMsg button {
  font-family: inherit;
  color: silver;
  font-weight: 700;
  background-color: rgb(66,66,66);
  width: 10%;
  margin-top: 20px;
  border: 0px;
  transition: all 0.3s;
  border-radius: 0px 10px 10px 0px;
}

.messenger .sendMsg button:hover,
.messenger .sendMsg button:focus {
  background-color: rgb(132,132,132);
  color: black;
  cursor: pointer;
}

.messenger .sendMsg button:active {
  -webkit-filter: brightness(.7);
          filter: brightness(.7);
}

@media (max-height: 800px) {
  .messenger .showMsg {
  height: 55vh;
}

.messenger .sendMsg textarea,
.messenger .sendMsg button {
  height: 15vh;
}
}

@media (max-height: 650px) {
section.messenger {
  padding-top: 4rem;
}

.messenger .showMsg {
height: 50vh;
}

.messenger .sendMsg textarea,
.messenger .sendMsg button {
  height: 13vh;
}
}

@media (max-height: 500px) {
section.messenger {
  padding-top: 3rem;
}

.messenger .showMsg {
height: 58vh;
}

.messenger form {
  display: flex;
}

.messenger .sendMsg input {
  width: 20%;
  margin-right: 2%;
}

.messenger .sendMsg .msgText {
  width: 78%;
}

.messenger .sendMsg input,
.messenger .sendMsg textarea {
  font-size: 14px;
  padding: 10px;
}

  .messenger .sendMsg input,
  .messenger .sendMsg textarea,
  .messenger .sendMsg button {
    height: 65px;
  }
}

@media (max-height: 400px) {
  .messenger .showMsg {
    height: 55vh;
  }

  .messenger .sendMsg input,
  .messenger .sendMsg textarea,
  .messenger .sendMsg button {
    height: 50px;
  }
}

@media (max-height: 350px) {
  .messenger .showMsg {
    height: 50vh;
  }
}

@media (max-width:1200px) {
  html {
    font-size: 10px;
  }

  ul.nav li {
    padding: 2rem;
    font-size: 2rem;
    height: 6rem;
  }
}

@media (max-width:800px) {
  ul.nav {
    display: flex;
    width: 100vw;
    z-index: 10;
    min-height: 0px;
    border-bottom: 1px solid #000;
    border-right: none;
  }

  ul.nav li {
    border-bottom: none;
    border-right: 1px solid #000;
    width: 100px;
    font-size: 16px;
    padding: 20px;
    height: 60px;
  }

  section {
    left: 0;
    width: 100vw;
    top: -3000px;
  }

  section.home footer {
    bottom: 70px;
  }

  section.messenger {
    padding: 4rem 3rem;
  }

  section.show {
    left: 0px;
    top: 60px;
  }

  .messenger h2 {
    font-size: 20px;
  }

  .messenger p {
    font-size: 16px;
  }

  .messenger .showMsg {
    padding: 10px;
  }

  .messenger ul.oldMsg li {
    margin: 5px auto;
    padding: 10px;
  }

  .messenger .sendMsg input,
  .messenger .sendMsg textarea {
    padding: 25px;
  }

  .messenger .sendMsg input,
  .messenger .sendMsg button {
    font-size: 16px;
  }

  .messenger .sendMsg textarea {
    width: 85%;
  }

  .messenger .sendMsg button {
    width: 15%;
  }
}

@media (max-width: 450px) {
  ul.nav li {
    padding: 10px;
    font-size: 16px;
  }

  section.messenger {
    padding: 3rem 2rem;
  }

  .messenger h2 {
    font-size: 16px;
  }
  
  .messenger p {
    font-size: 14px;
  }

  .messenger .sendMsg input,
  .messenger .sendMsg textarea {
    padding: 15px
  }

  .messenger .sendMsg input,
  .messenger .sendMsg button {
    font-size: 14px;
  }

  .messenger .sendMsg textarea {
    width: 80%;
  }

  .messenger .sendMsg button {
    width: 20%;
  }
}

@media (max-width:800px) and (max-height:800px) {
  .messenger .showMsg {
    height: 50vh;
  }

  .messenger .sendMsg textarea,
  .messenger .sendMsg button {
    height: 15vh;
  }
}

@media (max-width:800px) and (max-height:700px) {
  section.home h1{
    padding-top: 200px;
    font-size: 30px;
  }

  section.home h2{
    font-size: 20px;
  }

  .messenger .sendMsg textarea {
    padding: 15px;
  }

  .messenger .sendMsg textarea,
  .messenger .sendMsg button {
    height: 10vh;
  }
}

@media (max-width:800px) and (max-height:600px) {
  ul.nav li {
    padding: 10px;
    height: 45px;
  }

  section.show {
    top: 45px;
  }

  section.home h1{
    padding-top: 150px;
  }

  section.messenger {
    padding-top: 3.5rem;
  }
  
  .messenger .showMsg {
    height: 45vh;
  }

  .messenger .sendMsg textarea,
  .messenger .sendMsg button {
    height: 60px;
  }
}

@media (max-width:800px) and (max-height:500px) {
  section.home h1{
    padding-top: 100px
  }

  .messenger .showMsg {
    height: 50vh;
  }

  .messenger .sendMsg input,
  .messenger .sendMsg textarea,
  .messenger .sendMsg button {
    height: 50px;
  }
}

@media (max-width:800px) and (max-height:400px) {
  section.messenger {
    padding-top: 2rem;
  }

  .messenger .sendMsg input,
  .messenger .sendMsg textarea,
  .messenger .sendMsg button {
    margin-top: 10px;
  }

  .messenger .showMsg {
    height: 45vh;
  }
}
